.logo {
    display: inline-flex;
    align-items: center;
    padding: 16px 10px;

    & .title {
        font-size: 28px;
        font-weight: 500;
        color: var(--primary-color);
        margin-left: 13px;
    }
}

.side-bar {
    width: 330px;
    height: 100%;
    padding: 10px;
    background-color: #fff;

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;


    }

    &__search {
        margin-top: 14px;
    }

    &__add {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 20px 0;

        & .text {
            color: #555;
            font-size: 17px;
        }


    }

    &__list {
        height: calc(100vh - 215px);
        overflow-y: auto;
        position: relative;

        & .sub-text {
            color: #888;
            font-size: 14px;
            text-align: center;
            margin-top: 10px;

            & span {
                color: var(--primary-color);
                cursor: pointer;
            }
        }

        & .not-match {
            color: #999;
            text-align: center;
            font-size: 15px;
            margin-top: 10px;
        }

        &::-webkit-scrollbar {
            width: 6px;
        }

        &::-webkit-scrollbar-track {
            background: #f1f1f1;
        }

        /* Handle */
        &::-webkit-scrollbar-thumb {
            background: #aaa;
            border-radius: 10px;
        }

        /* Handle on hover */
        &::-webkit-scrollbar-thumb:hover {
            background: #999;
        }
    }

    // Responsive
    @media screen and (max-width: 767px) {
        width: 100%;
        position: fixed;
        bottom: 0;
        top: 0;
        left: 0;
        right: 0;
    }

}